import { z } from "zod";

export default z
  .object({
    type: z.literal("message_created"),
    data: z
      .object({
        id: z.string(),
        createdAt: z.string().datetime(),
        payload: z.union([
          z
            .object({ audioUrl: z.string().min(1), type: z.literal("audio") })
            ,
          z
            .object({
              title: z.string().min(1),
              subtitle: z.string().min(1).optional(),
              imageUrl: z.string().min(1).optional(),
              actions: z.array(
                z
                  .object({
                    action: z.enum(["postback", "url", "say"]),
                    label: z.string().min(1),
                    value: z.string().min(1),
                  })
                  
              ),
              type: z.literal("card"),
            })
            ,
          z
            .object({
              items: z.array(
                z
                  .object({
                    title: z.string().min(1),
                    subtitle: z.string().min(1).optional(),
                    imageUrl: z.string().min(1).optional(),
                    actions: z.array(
                      z
                        .object({
                          action: z.enum(["postback", "url", "say"]),
                          label: z.string().min(1),
                          value: z.string().min(1),
                        })
                        
                    ),
                  })
                  
              ),
              type: z.literal("carousel"),
            })
            ,
          z
            .object({
              text: z.string().min(1),
              options: z.array(
                z
                  .object({
                    label: z.string().min(1),
                    value: z.string().min(1),
                  })
                  
              ),
              type: z.literal("choice"),
            })
            ,
          z
            .object({
              text: z.string().min(1),
              options: z.array(
                z
                  .object({
                    label: z.string().min(1),
                    value: z.string().min(1),
                  })
                  
              ),
              type: z.literal("dropdown"),
            })
            ,
          z
            .object({
              fileUrl: z.string().min(1),
              title: z.string().min(1).optional(),
              type: z.literal("file"),
            })
            ,
          z
            .object({ imageUrl: z.string().min(1), type: z.literal("image") })
            ,
          z
            .object({
              latitude: z.number(),
              longitude: z.number(),
              address: z.string().optional(),
              title: z.string().optional(),
              type: z.literal("location"),
            })
            ,
          z
            .object({
              markdown: z.string().min(1),
              type: z.literal("markdown"),
            })
            ,
          z
            .object({ text: z.string().min(1), type: z.literal("text") })
            ,
          z
            .object({ videoUrl: z.string().min(1), type: z.literal("video") })
            ,
        ]),
        userId: z.string(),
        conversationId: z.string(),
      })
      ,
  })
  ;
